import { Component, HostBinding, OnInit, ViewChild, TemplateRef, HostListener, ElementRef,Input, Output, EventEmitter } from '@angular/core';
import { SignatureComponent } from '../signature/signature.component';

@Component({
  selector: 'wf-sign-here',
  templateUrl: './sign-here.component.html',
  styleUrls: ['./sign-here.component.css']
})
export class SignHereComponent implements OnInit {

  @ViewChild(SignatureComponent) signaturePad: SignatureComponent;
  @ViewChild('canvastitle') public canvasDiv: ElementRef;

  // æåºéä¸­çäºä»¶
  @Input() imageType = "image/png";
  @Input() imageQuality = 1;
  @Input() title='è¯·å¯¹å®¡æ¹æè§è¿è¡ç­¾å­ç¡®è®¤';
  @Input() image;
  isDraw = false;

  

  signaturePadOptions: Object = { // passed through to szimek/signature_pad constructor
    'minWidth': 1,
    'canvasWidth': 500,
    'canvasHeight': 240
  };

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    // this.signaturePad is now available
    this.signaturePad.set('minWidth', 1); // set szimek/signature_pad options at runtime
    this.signaturePad.set('canvasWidth', this.canvasDiv.nativeElement.clientWidth);
    this.signaturePad.clear(); // invoke functions from szimek/signature_pad API
    this.signaturePad.setImageBackground();
    if(this.image){
      const img = new Image();
      img.src=this.image;
      this.signaturePad.setBackgroundImage(img);
    }
  }

  confirm() {
    console.log(this.signaturePad.toDataURL(this.imageType,this.imageQuality));
    //this.signaturePad.setWaterMark(new Date());
    return this.signaturePad.toDataURL(this.imageType,this.imageQuality);
  }
  reset() {
    this.signaturePad.clear();
    this.signaturePad.setImageBackground();
    this.isDraw=false;
  }

  drawComplete() {
    this.isDraw=true;
    // will be notified of szimek/signature_pad's onEnd event
    //console.log(this.signaturePad.toDataURL());
  }

  drawStart() {
    // will be notified of szimek/signature_pad's onBegin event
    console.log('begin drawing');
  }

  //è®¾ç½®èæ¯å¾ç
  initCanvasByImage(image){
    this.signaturePad.setBackgroundImage(image);
  }

}
